import React from 'react'
import { Link } from 'react-router-dom'
import './home.css'

export default function Home() {
  return (
    <div className='home'>
        <h1>WELCOME TO CHATSCRUM</h1>
        <div className='links'>
            <Link to="/sign-up"><h4>SIGN UP</h4></Link>
            <Link to='/sign-in'><h4>SIGN IN</h4></Link>
        </div>
    </div>
  )
}
