import React from "react";
import './signup.css';
import content from "../../static";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup"
import * as yup from "yup"
import { Link } from 'react-router-dom'

export default function SignUp(){


    const schema = yup.object().shape(
        {
            fullname: yup.string().required().min(6),
            email: yup.string().required('please enter a valid email'),
            password: yup.string().required("Please enter password").matches(
                /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}/,
            "Must contain 8 characters, One Capital Letter, One Lowercase, One Number and One Special Case Character")
        }
    )

    const {register, handleSubmit, formState: {errors}} = useForm(
        {
            resolver: yupResolver(schema)
        }
    )

    const onSubmit = (data) => console.log(data)

    return(
        <div className="sign-up">
        <h1>Don't have an account?</h1>
        <h2>Sign up here!</h2>
        <form onSubmit={handleSubmit(onSubmit)}>
            {content.inputs.map((input, key) =>{
                return(
                    <div key={key}>                        
                        <label htmlFor={input.name}>{input.label}</label>
                        <br/>
                        <input type={input.type} name={input.name} {...register(input.name)}/>
                        <br/>
                        <span className="message">{errors[input.name] && errors[input.name].message}</span>
                    </div>
                )
            })}            
            <label for="options">User Type:</label>
            <select id="options">
                <option value="Developer">Developer</option>
                <option value="Owner">Owner</option>
            </select>
            <br/>
            <button>SIGN UP</button>
            </form>
            <p>Have an account? <Link to='/sign-in'><span className="sign">Sign In</span></Link>.</p>
            <p>Back to <Link to='/react-validation-form'>Home</Link></p>
        </div>
    )
}
