import React from "react";
import './sign-in.css';
import { Link } from "react-router-dom";

export default function SignIn(){
    return(
        <div className="sign-in">
            <h1>Already have an account?</h1>
            <h2>Sign In here</h2>
            <form>
                <label for="email">Email Address:</label>
                <input type="email" name="email" placeholder="Enter your email"/>
                <br/>
                <label for="password">Password:</label>
                <input type="password" name="password" placeholder="Enter your password"/>
                <br/>
                <label for="text">Project Name:</label>
                <input type="text" name="text" placeholder="Enter project name"/>
                <br/>
                <button>SIGN IN</button>
            </form>
            <p>Don't have an account? <Link to='/sign-up'><span className="sign">Sign Up</span></Link>.</p>
            <p>Back to <Link to='/react-validation-form'>Home</Link></p>
        </div>
    )
}
